import { Injectable } from '@angular/core';

import { NavLinkModel } from '../models/nav-link.model';

@Injectable({
    providedIn: 'root'
  })
export class NavLinksService {
  links: NavLinkModel[] = [];
  uniteMainLinks: NavLinkModel[] = [];
  uniteMoreLinks: NavLinkModel[] = [];
  careerActionLinks: NavLinkModel[] = [];
  educationActionLinks: NavLinkModel[] = [];

  constructor() {
    this.makeGeneralLinks();
    this.makeUniteMainLinks();
    this.makeCareerActionLinks();
    this.makeUniteFeaturesLinks();
    this.makeEducationActionLinks();
  }

  makeGeneralLinks() {
    this.links.push({
      label: 'Browse Content',
      icon: 'general',
      route: '/posts',
      state: 'active'
    });
    this.links.push({
      label: 'Education',
      icon: 'education',
      route: '/education/type',
      state: 'inactive'
    });
    this.links.push({
      label: 'Career',
      icon: 'careers',
      route: '/career',
      state: 'inactive'
    });
    this.links.push({
      label: 'Events',
      icon: 'events',
      route: '/events',
      state: 'inactive'
    });
    this.links.push({
      label: 'UNITE',
      icon: 'unite',
      route: '/unite',
      state: 'inactive'
    });
    this.links.push({
      label: 'My Profile',
      icon: 'profile',
      route: '/profile',
      state: 'inactive'
    });
    this.links.push({
      label: 'Settings',
      icon: 'setting',
      route: '/settings',
      state: 'inactive'
    });
    this.links.push({
      label: 'Logout',
      icon: 'logout',
      route: '',
      state: 'inactive'
    });
  }

  makeUniteMainLinks() {
    this.uniteMainLinks.push({
      label: 'Go to Bookmarks',
      icon: 'assets/images/profile/arrow-right.png',
      route: '/unite/bookmark',
      state: 'active'
    });
  }

  makeUniteFeaturesLinks() {
    this.uniteMoreLinks.push({
      label: 'Search',
      icon: 'assets/images/unite/search.png',
      route: '/unite/search',
      state: 'active'
    });

    this.uniteMoreLinks.push({
      label: 'Thumbnails',
      icon: 'assets/images/unite/thumbnail.png',
      route: '/unite/thumbnail',
      state: 'active'
    });

    this.uniteMoreLinks.push({
      label: 'Fullscreen',
      icon: 'assets/images/unite/full-screen.png',
      route: '/unite/view',
      state: 'active'
    });

    this.uniteMoreLinks.push({
      label: 'Go to Bookmarks',
      icon: 'assets/images/profile/arrow-right.png',
      route: '/unite/bookmark',
      state: 'active'
    });
  }

  makeCareerActionLinks() {
    this.careerActionLinks.push({
      label: 'Explore',
      icon: 'explore',
      route: '/career',
      state: 'active'
    });
    this.careerActionLinks.push({
      label: 'Find Job',
      icon: 'find',
      route: '/career/search',
      state: 'inactive'
    });
    this.careerActionLinks.push({
      label: 'My Jobs',
      icon: 'my-jobs',
      route: '/career/my-job',
      state: 'inactive'
    });
    this.careerActionLinks.push({
      label: 'Alerts',
      icon: 'bottom_alert',
      route: '/career/alert',
      state: 'inactive'
    });
    this.careerActionLinks.push({
      label: 'More',
      icon: 'more',
      route: '#more',
      state: 'inactive'
    });
  }

  makeEducationActionLinks() {
    this.educationActionLinks.push({
      label: 'Catalog',
      icon: 'catalog',
      route: '/education/type',
      state: 'active'
    });
    this.educationActionLinks.push({
      label: 'Search',
      icon: 'search',
      route: '/education/search',
      state: 'inactive'
    });
    this.educationActionLinks.push({
      label: 'Bookmarks',
      icon: 'bookmark',
      route: '/education/bookmarks',
      state: 'inactive'
    });
    this.educationActionLinks.push({
      label: 'Downloads',
      icon: 'downloads',
      route: '/education/downloads',
      state: 'inactive'
    });
    this.educationActionLinks.push({
      label: 'My Courses',
      icon: 'my-courses',
      route: '/education/courses',
      state: 'inactive'
    });
  }

  getNavLinks() {
    return this.links;
  }

  initNavLinks() {
    this.links.map(link => {
      link.state = 'inactive';
    });
  }

  setNavLink(link: NavLinkModel) {
    this.links.map(item => {
      if (item === link) {
        if (item.icon !== 'logout') {
          item.state = 'active';
        } else {
          this.links[0].state = 'active';
        }
      }
    });
  }
}
